<template>
  <div class="g-notice scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
      <div class="m-box" v-for="(v,i) in list">
        <div class="s-title">{{v.name}}</div>
        <div class="s-content">
          {{v.content}}
        </div>
        <div class="s-time">通知时间：{{v.time}}</div>
      </div>
    </cube-scroll>
    <div class="m-bk"></div>
    <nocontent net="暂无公告~" v-show="list.length==0"/>
  </div>
</template>

<script>
  import nocontent from "@/components/nocontent"
  export default {
    name: "notice",
    data() {
      return {
        list: [],/*公告数据*/
        options:{/*滚动配置*/
          scrollbar:{fade:true},
          pullUpLoad:{txt:{ noMore: '没有更多数据了' }},
          pullDownRefresh:{stopTime:0,txt:' '}
        },
        paged:1
      }
    },
    created(){
      this.notice(this.paged);//公告数据
    },
    methods: {
      notice(i){//内容加载
        this.$post(this.$bee+'/api/index/listNotice',{page:i}).then((data)=>{
          if(data.code==200&&data.data.notices.length!=0){
            data.data.notices.forEach((i)=> {
              this.list.push({content: i.content, name: i.title,time:i.createTimeStr});
            })
            this.paged++;
          }else{
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {
        this.paged=1;
        this.list=[];
        this.notice(this.paged);
      },
      onPullingUp() {
        this.notice(this.paged);
      }
    },
    components: {
      nocontent
    }
  }
</script>

<style scoped lang="stylus">
  .m-bk
    position fixed
    z-index -100
    top 0
    bottom 0
    left 0
    right 0
    background #f2f2f2

  .m-box
    width: 343px;
    background-color: #ffffff;
    border-radius: 5px;
    border: solid 1px #e3e3e3;
    margin 10px auto 0
    padding 15px
    box-sizing border-box
    text-align left
    &:last-child
      margin-bottom 10px
    .s-title
      line-height 1.3
      color: #000000;
      font-size: 14px;
    .s-content
      color: #4d4d4d;
      font-size 12px
      line-height 1.2
      margin-top 15px
    .s-time
      font-size: 12px;
      color: #1a1a1a;
      margin-top 10px
</style>
